import React, { Component } from 'react';
import {Link} from "react-router-dom";

class Home extends Component {
    render() {
    
        return (
          <div className="container-fluid">
          <div className="row mx-auto style=">
                  <div className="col-md-8 col-sm-8 offset-sm-2 offset-md-2 pt-3"><div className="card">
                      <div className="card-header text-center pb-2 bg-info">
                       <h3>Welcome to My Personal Page!</h3>
                      </div>
                      <div className="card-body text-center">
                        <h5 className="card-title">You can find out things about me on this website. Thank you for visiting.</h5>
                        <p className="card-text">Hello! My name is Ömer Onur.
                        I am 22 years old student who trying to learn JavaScript.
                        I am studying at Aksaray University in Aksaray, Turkey. 
                        My department is Electrical and Electronics Engineering, 
                          so I have to learn new things and catch the technology.
                        This is the reason of the my interest of JavaScript. 
                        I think, we all have to catch the technology because it is improving at the speed of light. 
                        Therefore I am full-focused for learning new things and using actively.
                        I think, I am on the right way. 
                        You can see my biography and more about me at the 'About Me' page.
                        </p>
                        <Link to="/about" className="btn btn-success">
                        <small className="text-center">Click here for more.</small></Link>
                        <br/>
                        <Link to="*" className="btn btn-primary mt-3">
                        <small className="spinner-grow spinner-grow-sm" role="status" aria-hidden="true"></small><small className="text-right text-monospace">click here for test my new 404 page</small><small className="spinner-grow spinner-grow-sm" role="status" aria-hidden="true"></small></Link>
                      </div>
                    </div></div>
              </div>
              </div>
        );
    }
}

export default Home;