import React, { Component } from 'react';
import { Link } from 'react-router-dom';

    

class Notfound extends Component {
    
    render() {
        
        return (
            <div className="container">
            <div className="notfound-copy">
                <img className="col-md-12 col-sm-12" src="https://www.404.ie/assets/img/logo_blue.png" alt=""/>
       <div className="row"> 
       <div className="text-monospace text-center col-md-8 col-sm-8 offset-sm-2 offset-md-2 pt-3">
        <h1>page not found</h1><hr/>
        </div>
        
        <div className="col-md-8 col-sm-8 offset-sm-2 offset-md-2 pt-3">
        <h6 className="bg-dark text-light btn text-center text-monospace font-weight-bold">
            <p><em>this may</em> not <mark>mean</mark> anything.
            I'm <s>probably</s> working <mark>on</mark> <u>something</u> that has <strong>blown up.</strong></p></h6><hr/>
            </div>
            </div>
            
            <div className="row mt-4">
                
                <div className="col-6">
            <Link to ="/" className="text-monospace btn-lg btn-block btn-outline-success text-center" active><i class="fas fa-angle-double-right"></i> Go back to homepage <i class="fas fa-angle-double-left"></i></Link>
  {' '}<hr/></div> 
  <div className="col-6">
 
  <Link to ="/contact" className="text-info text-monospace btn-lg btn-block text-center btn-outline-warning" active><i class="fas fa-angle-double-right"></i> Click here to contact me <i class="fas fa-angle-double-left"></i></Link>
  <hr/>
  </div>
  </div>
          
          
      </div>
      </div>
        );
    }
}
export default Notfound;


